
import { defineComponent } from 'vue';
import * as API from '@/API';

export default defineComponent({
  data() {
    return {
      password: '',
      email: '',
      error: false,
      loggingIn: false,
    };
  },
  methods: {
    async login() {
      this.error = false;
      this.loggingIn = true;
      if (await API.login(this.email, this.password)) {
        console.log('admin logged in with:' + this.email);
        this.email = '';
        this.password = '';
        this.$router.push('/');
      } else {
        this.password = '';
        this.error = true;
      }
      this.loggingIn = false;
    },
  },
});
